	module.exports = {"data":{"talkJson":{"description":"How humans learn and how to build user interfaces that support it. “The parts of the body you want to have learn don’t understand English.”","meta":{"duration":"PT48M21S"},"presenter":{"name":"Alan Kay"},"slug":"doing-with-images-makes-symbols","source":{"id":"kzDpfk8YhlE","provider":"youtube"},"tag":["User Interface","UX"],"title":"Doing with Images Makes Symbols"},"allTalkJson":{"edges":[{"node":{"description":"Check out our upcoming conferences at http://gotocon.com\nSubscribe to the GOTO Conferences YouTube Channel at https://www.youtube.com/user/GOTOConf...\n\nJanne Jul Jensen - Interaction Designer and Usability Specialist\n\nABSTRACT\nMost developers today are aware of the importance of creating a good user interface with a high level of usability, but many are lacking the methods and techniques that can help in this process. This session will present to the listeners a range of concrete methods and techniques applicable in different phases of a design process, to handle specific challenges. This will include design patterns, personas, wire framing, paper prototype testing, progressive disclosure, card sorting and creative workshops and many of the methods and techniques will be accompanied by examples.\n\nhttps://twitter.com/gotocon\nhttps://www.facebook.com/GOTOConference\nhttp://gotocon.com","meta":{"duration":"PT51M20S"},"presenter":{"name":null},"source":{"id":"7OSkB4BCx00","provider":"youtube"},"slug":"user-interface-ux-techniques-janne-jul-jensen","tag":["Software Development","Janne Jul Jensen","User Interface Design (Field Of Study)","User Interface (Ontology Instance)","User Interface","GOTO Conference","GOTO Conference","GOTO Conference","User Experience Design (Industry)","Technology (Industry)","User Interface (Speech Topic)","UX","Trifork","Design (Industry)","Programming Language","Videos for Developers"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/7OSkB4BCx00/default.jpg","height":90,"width":120}},"title":"User Interface (UX) Techniques • Janne Jul Jensen"}},{"node":{"description":"It's not a demo, more of a philosophical argument: Why did Sergey Brin and his team at Google want to build an eye-mounted camera/computer, codenamed Glass? Onstage at TED2013, Brin calls for a new way of seeing our relationship with our mobile computers -- not hunched over a screen but meeting the world heads-up.","meta":{"duration":"PT7M16S"},"presenter":{"name":"Sergey Brin"},"source":{"id":"IessjPY9gwI","provider":"youtube"},"slug":"why-google-glass","tag":["TEDTalks","Design","User Interface","Google"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/IessjPY9gwI/default.jpg","height":90,"width":120}},"title":"Why Google Glass?"}}]}},"pathContext":{"slug":"doing-with-images-makes-symbols","tags":["User Interface","UX"]}}

